/* Contenedor general */
.secciones {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* 2 columnas en PC */
    gap: 30px;
    max-width: 1200px;
    margin: 0 auto;
    padding: 40px 20px;
}

/* Hero */
.hero {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 50px;
    flex-wrap: wrap; /* Esto permite que se ajusten en pantallas pequeñas */
    color: #f0f0f0;
    max-width: 100%; /* Asegura que el contenedor ocupe todo el ancho disponible */
}

/* Texto */
.hero-texto {
    flex: 1;
}

.hero-texto h1 {
    font-size: 2.3rem;
    margin-bottom: 15px;
    line-height: 1.2;
}

.hero-texto h1 span {
    color: #1ce497;
    text-shadow: 0px 2px 8px rgba(28, 228, 151, 0.6);
}

.hero-texto p {
    font-size: 1rem;
    line-height: 1.6;
    margin-bottom: 25px;
    color: #ddd;
}

.hero-texto .highlight {
    color: #1ce497;
    font-weight: 600;
}

/* Derecha: Imagen + Iconos */
.hero-derecho {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

/* Imagen de perfil */
.hero-img img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
    border: 3px solid rgba(28, 228, 151, 0.8);
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.6);
    transition: transform 0.4s ease;
}

.hero-img img:hover {
    transform: scale(1.05);
}

/* Contactos debajo de la imagen */
.contactos {
    display: flex;
    gap: 25px;
}

.contactos a ion-icon {
    font-size: 2rem;
    transition: transform 0.3s ease, filter 0.3s ease;
}

.contactos a:hover ion-icon {
    transform: scale(1.2);
    filter: drop-shadow(0px 0px 8px rgba(28, 228, 151, 0.8));
}

.contactos a i {
    font-size: 2.5rem;
    color: #ddd;
    transition: transform 0.3s ease, filter 0.3s ease, color 0.3s ease;
    cursor: pointer;
}

/* Efecto hover para iconos */
.contactos a:hover i {
    transform: scale(1.2);
    color: #1ce497;
    filter: drop-shadow(0 0 8px rgba(28, 228, 151, 0.8));
}

/* 📱 Responsive */
@media (max-width: 992px) {
    .secciones {
        grid-template-columns: repeat(2, 1fr); /* 2 columnas en tablets */
    }
}

@media (max-width: 600px) {
    .secciones {
        grid-template-columns: 1fr; /* 1 columna en celular */
    }
}